.custom_dialog {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);

  :global {
    .custom_dialog-warp {
      width: 540px;
      background: var(--g-theme-dialogBg);
      color: var(--g-theme-color);
      padding: 20px;
      padding-bottom: 40px;
      box-sizing: border-box;

      border-radius: 20px;
      margin: 0 auto;
      margin-top: 20vh;


      @media only screen and (max-width: 600px) {
        width: 80%;
      }

      .custom_dialog-head {
        display: flex;
        justify-content: flex-end;
        align-items: center;


        .closeBtn {
          cursor: pointer;
        }
      }

      .title {
        width: 100%;
        font-size: 24px;
        font-weight: bold;
        text-align: center;
      }

      .custom_dialog-content {
        width: 100%;
        margin-top: 20px;
        max-height: 60vh;
        overflow: auto;

        .user_detail_bottom {
          width: 100%;
          display: flex;
          justify-content: center;
          margin-top: 20px;

          .tolls_btn {
            margin: 0 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
@primary-color: #1DA57A;