.custom_dialog {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);

  :global {
    .custom_dialog-warp {
      width: 600px;
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: var(--g-theme-dialogBg);
      color: var(--g-theme-color);
      padding: 20px;
      border-radius: 10px;

      @media only screen and (max-width: 600px) {
        width: 80%;
      }

      .custom_dialog-head {
        display: flex;
        justify-content: flex-end;
        align-items: center;



        .closeBtn {
          cursor: pointer;
        }
      }

      .title {
        width: 100%;
        font-size: 24px;
        font-weight: bold;
        text-align: center;
      }

      .custom_dialog-content {
        width: 100%;
        margin-top: 20px;

        .product_info {
          width: 100%;
          text-align: center;
          font-size: 18px;

          .active {
            color: #edab38;
          }
        }

        .payLoading {
          width: 100%;

          .payLoading_info {
            width: 100%;
            text-align: center;
            margin-top: 20px;
          }
        }

        .rightListWarp {
          width: 50%;
          margin: 0 auto;
          text-align: center;
          margin-bottom: 20px;

          .rightList_warp-title {
            font-size: 18px;
            color: #edab38;
            margin-bottom: 10px;
            display: flex;
            justify-content: center;
            align-items: center;

            .diamondVipIcon {
              width: 25px;
              height: 25px;
              margin-right: 5px;
            }
          }

          .rightListitem{
            text-align: left;
          }
        }

        .pay_method {
          width: 100%;
          display: flex;
          justify-content: center;
        }

        .qrCode_box {
          text-align: center;
          margin-top: 20px;

          .pay_title {
            font-size: 20px;
            color: #edab38;
            font-weight: bold;
          }
        }

        .user_detail_bottom {
          width: 100%;
          display: flex;
          justify-content: center;
          margin-top: 20px;

          .tolls_btn {
            margin: 0 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
@primary-color: #1DA57A;